.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }
/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: #373a47;
    }
.bm-item {
    display: inline-block;
    text-decoration: none;
    margin-bottom: 15px;
    color: white;
    transition: color 0.2s;
    font-size: 1.7em;
  }
  
  .bm-item:hover {
    color: darkgray;
    font-size: 1.9em;
  }
  

  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
  